import React, { useEffect } from "react";
import "../style/About.css";
function About() {
  useEffect(() => {
    let navbarHeight = document.querySelector(".navbar-contact-wrapper");
    if(navbarHeight.offsetHeight > navbarHeight.clientHeight){
      navbarHeight = navbarHeight.offsetHeight;
    }
    else{
      navbarHeight = navbarHeight.clientHeight;
    }
    document.querySelector(
      ".about-container"
    ).style = `margin-top : ${navbarHeight}px`;
  }, []);
  return (
    <div className="about-container">
      <div className="about-header">
        <header>Qui Sommes Nous ?</header>
      </div>
      <div className="about-details">
        <p>
          <strong>WRSConsulting</strong> est une socit qui a t cre en 2010
          pour rpondre aux besoins en service informatique de clients de divers
          secteurs dactivit (ex : banques/assurances, Groupes
          pharmaceutiques).
        </p>
        <p>
          <strong>WRSConsulting</strong> est base  Paris et collabore avec des
          clients aussi bien nationaux quinternationaux, sur des problmatiques
          dingnierie de systmes complexes et hautement stratgiques. Depuis
          2010, nous navons de cesse de collaborer  la refonte et 
          loptimisation des processus mtier de nos clients.
        </p>
      </div>
    </div>
  );
}

export default About;
